<template>
  <div name="PostArticle" class="PostArticle-wrap">
    <el-row style="margin: 10px 0 50px 50px">
      <font class="title">上传、转载文章：</font>
    </el-row>

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <div class="forms">
        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font>是否为转载文章？</font>
          <div
            style="display: flex; width: 400px; justify-content: space-between"
          >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="1"
              border
              >是</el-radio
            >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="2"
              border
              >否</el-radio
            >
          </div>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>* </span>作者名称：</font>
          <el-form-item prop="author_name">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.author_name"
              placeholder="请输入本文的作者名称(转载其他网站的文章需填入)"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font>作者头像：</font>
          <el-form-item prop="author_head_photo">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.author_head_photo"
              placeholder="有条件的可以上传头像的链接~通过f12查看元素即可复制"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>* </span>转载网址：</font>
          <el-form-item prop="article_link">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_link"
              placeholder="请输入发表本文的原网址(转载其他网站的文章需填入)"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>* </span>文章标题：</font>
          <el-form-item prop="article_title">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_title"
              placeholder="请输入文章标题"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>* </span>文章所属专业：</font>
          <el-cascader
            style="width: 400px; height: 70px"
            v-model="ruleForm.article_major"
            placeholder="请选择文章所属的专业类别"
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            :value="value"
          ></el-cascader>
        </el-row>

        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font>文章标签：</font>
          <el-select
            v-model="ruleForm.article_labels"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="请输入文章标签(自定义)"
            style="width: 400px"
          >
            <el-option
              v-for="item in optionsSubject"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 900px">
          <font><span>* </span>文章内容简介：</font>
          <el-form-item prop="article_introduce">
            <el-input
              style="width: 700px"
              class="public-uncheck"
              v-model="ruleForm.article_introduce"
              placeholder="请简短的介绍一下你的文章内容(如果文章标题已经概括，那么直接用标题也可)"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row>
          <el-col :span="0.5">
            <font><span>* </span>正文内容：</font>
          </el-col>
        </el-row>

        <el-row type="flex" justify="space-between">
          <el-form-item prop="article_details">
            <mavon-editor
              style="min-height: 500px; max-height: 800px; min-width: 1100px"
              ref="md"
              @change="change"
              v-model="ruleForm.article_details"
            />
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="end" style="margin: 30px 60px 0 0">
          <div class="submit" @click="submitArticle()">
            <font>提交文章</font>
          </div>
        </el-row>
      </div>
    </el-form>
  </div>
</template>

<script>
import { postArticleListAPI } from "@/api/homeAPI/articleslist";
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "PostArticle",
  data() {
    return {
      optionsSubject: [],
      value: [],
      options: [
        {
          value: "文学",
          label: "文学",
          children: [
            {
              value: "中国语言文学类",
              label: "中国语言文学类",
            },
            {
              value: "新闻传播学类",
              label: "新闻传播学类",
            },
            {
              value: "外国语言文学类",
              label: "外国语言文学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "艺术学",
          label: "艺术学",
          children: [
            {
              value: "音乐",
              label: "音乐",
            },
            {
              value: "舞蹈",
              label: "舞蹈",
            },
            {
              value: "影视学类",
              label: "影视学类",
            },
            {
              value: "戏剧学类",
              label: "戏剧学类",
            },
            {
              value: "美术学类",
              label: "美术学类",
            },
            {
              value: "设计学类",
              label: "设计学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "理学",
          label: "理学",
          children: [
            {
              value: "信息与计算科学",
              label: "信息与计算科学",
            },
            {
              value: "数学与应用数学",
              label: "数学与应用数学",
            },
            {
              value: "应用化学",
              label: "应用化学",
            },
            {
              value: "生物技术",
              label: "生物技术",
            },
            {
              value: "生物科学",
              label: "生物科学",
            },
            {
              value: "应用物理学",
              label: "应用物理学",
            },
            {
              value: "应用心理学",
              label: "应用心理学",
            },
            {
              value: "统计学",
              label: "统计学",
            },
            {
              value: "化学",
              label: "化学",
            },
            {
              value: "物理学",
              label: "物理学",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "工学",
          label: "工学",
          children: [
            {
              value: "土木工程",
              label: "土木工程",
            },
            {
              value: "机械类专业",
              label: "机械类专业",
            },
            {
              value: "电气工程及其智能化",
              label: "电气工程及其智能化",
            },
            {
              value: "计算机科学与技术",
              label: "计算机科学与技术",
            },
            {
              value: "车辆工程",
              label: "车辆工程",
            },
            {
              value: "软件工程",
              label: "软件工程",
            },
            {
              value: "交通运输工程",
              label: "交通运输工程",
            },
            {
              value: "自动化",
              label: "自动化",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "农学",
          label: "农学",
          children: [
            {
              value: "园艺",
              label: "园艺",
            },
            {
              value: "种子科学与工程",
              label: "种子科学与工程",
            },
            {
              value: "设施农业科学与工程",
              label: "设施农业科学与工程",
            },
            {
              value: "农业资源与环境",
              label: "农业资源与环境",
            },
            {
              value: "动物科学",
              label: "动物科学",
            },
            {
              value: "林学",
              label: "林学",
            },
            {
              value: "水产养殖学",
              label: "水产养殖学",
            },
            {
              value: "草业学科",
              label: "草业学科",
            },
            {
              value: "茶学",
              label: "茶学",
            },
            {
              value: "烟草",
              label: "烟草",
            },
            {
              value: "农艺教育",
              label: "农艺教育",
            },
            {
              value: "园艺教育",
              label: "园艺教育",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "其他学科",
          label: "其他学科",
        },
      ],
      ruleForm: {
        // 文章信息
        article_status: "1", //默认为转载
        article_link: "",
        article_title: "",
        article_major: "",
        article_labels: [],
        article_introduce: "",
        article_details: "",

        // 作者信息
        author_id: 0, //默认为转载
        author_status: 0, //默认为转载
        author_head_photo: "",
        author_name: "",
        article_num: 0,

        //初始化文章相应的状态信息
        collection_num: 0,
        comment_num: 0,
        like_num: 0,
        share_num: 0,
      },
      html: "", // 通过markdown及时转的html
      rules: {
        article_title: [
          { required: true, message: "请输入文章标题", trigger: "blur" },
        ],
        article_details: [
          { required: true, message: "请输入文章详细内容", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    // 所有操作都会被解析重新渲染
    change(_, render) {
      // render 为 markdown 解析后的结果[html]
      this.html = render;
    },
    submitArticle() {
      if (
        (this.ruleForm.article_status == "1" &&
          this.ruleForm.author_name &&
          this.ruleForm.article_link &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details) ||
        (this.ruleForm.article_status == "2" &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details)
      ) {
        // 统一处理ruleForm
        this.ruleForm.article_details = this.html;
        // 如果未上传作者头像，且为转载文章，则赋值为默认头像；原创文章则为空即可
        if (
          !this.ruleForm.author_head_photo &&
          this.ruleForm.article_status == "1"
        ) {
          this.ruleForm.author_head_photo = "https://dummyimage.com/400X400";
        }
        if (this.ruleForm.article_status == "2") {
          this.ruleForm.author_status = 1;
          this.ruleForm.author_id = JSON.parse(
            localStorage.getItem("user_info")
          ).id;
        }
        // 若检测到用户输入的标签还未存储，则将新标签加入本地
        let article_labels = [];
        if (localStorage.getItem("article_labels")) {
          for (
            var i = 0;
            i < JSON.parse(localStorage.getItem("article_labels")).length;
            i++
          ) {
            article_labels.push(
              JSON.parse(localStorage.getItem("article_labels"))[i]
            );
          }
        }
        let same = [];
        article_labels.forEach((item1) => {
          this.ruleForm.article_labels.forEach((item2) => {
            if (item1.value == item2) {
              console.log(item2);
              same.push(item2);
            }
          });
        });
        if (same.length != 0) {
          // 进行筛选不同词
          let result = this.ruleForm.article_labels.filter((item1) => {
            return same.every((item2) => {
              return item1 != item2;
            });
          });
          // console.log("筛选结果！", result);
          result.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          console.log(article_labels);
        } else {
          // console.log("无重复！", this.ruleForm.article_labels);
          this.ruleForm.article_labels.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          console.log(article_labels);
        }
        localStorage.setItem("article_labels", JSON.stringify(article_labels));

        postArticleListAPI(this.ruleForm).then((res) => {});
        // 将表单初始化
        this.ruleForm = {
          article_title: "",
          article_major: "",
          article_details: "",
          article_num: 0,
          author_head_photo: "",
          author_name: "",
          collection_num: 0,
          comment_num: 0,
          like_num: 0,
          share_num: 0,
        };
        // 显示提交成功
        this.$notify({
          title: "发布成功！",
          message: "快快前往首页看看吧！",
          type: "success",
        });
      } else {
        // 显示必要项未填满
        this.$notify({
          title: "发布文章失败！",
          message: "必填项未填满！",
          type: "error",
        });
      }
    },
  },
  mounted() {
    if (localStorage.getItem("article_labels")) {
      for (
        var i = 0;
        i < JSON.parse(localStorage.getItem("article_labels")).length;
        i++
      ) {
        this.optionsSubject.push(
          JSON.parse(localStorage.getItem("article_labels"))[i]
        );
      }
    }
  },
  components: { mavonEditor },
};
</script>
<style scoped>
.PostArticle-wrap {
  margin-bottom: 30px;
  padding: 20px;
  width: 1300px;
  border-radius: 5px;
  background: #ffffff;
}
.title {
  font-family: SourceHanSansCN-Bold;
  font-size: 36px;
  font-weight: bold;
  color: #3d3d3d;
}
.forms {
  margin: 0 0 0 100px;
}
.forms > * {
  margin: 20px 0 20px 0;
}
.forms > * font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
}
.forms > * font span {
  color: #ff8200;
}
.submit {
  width: 150px;
  height: 60px;
  border-radius: 30px;
  background: #76fff5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}
.submit font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  color: #3d3d3d;
  transition: all 0.5s;
}
.submit:hover {
  background-color: #00ead8;
}
.submit:hover font {
  color: #ffffff;
}
</style>